%overlay,
.overlay {
  position: relative;
  img {
    z-index: -2;
    position: relative;
  }
  &:after {
    background: $btnGradientBg;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    z-index: -1;
  }
}
